import { Component, OnInit } from '@angular/core';
import { routerTransition } from '../../router.animations';
import { TwitterService } from '../../shared/services/twitter/twitter';
import { NoticiasService } from '../../shared/services/noticias/noticias';
import { PresidenteNoticias } from '../noticias/noticias.model';
import { TwitterModel } from '../twitter/twitter.model';

@Component({
    selector: 'app-bolsonaro-page',
    templateUrl: './joao.component.html',
    styleUrls: ['./joao.component.scss'],
    animations: [routerTransition()]
})
export class JoaoComponent implements OnInit {
    wordCloud: string;
    countTw = 0;
    countNt = 0;

    public pieChartLabels: string[] = [
        'Negativo',
        'Positivo'
    ];
    public pieChartData: number[] = [300, 500];
    public pieChartType = 'pie';
    constructor(public twitter: TwitterService, 
        public noticias: NoticiasService, 
        public modelNews: PresidenteNoticias,
        public twitterModel: TwitterModel) {
        this.searchTwitter();
        this.searchNews();
    }

    searchTwitter() {
        this.wordCloud = 'http://35.237.82.186/app1/wcloud?pres=amoedo';
        this.twitter.getSentimento('amoedo').then(data => {
            this.countTw = data[0].n + data[1].n;
            this.pieChartData = [data[0].n, data[1].n];
        });

        this.twitter.getNumMencoes('amoedo').then(data => {
            this.countTw = data[0].n;
        });
        
    }

    searchNews() {
        this.noticias.getMencoes('amoedo').then(data => {
            for(let x of data) {
                this.countNt += x.n;
            }
            this.modelNews.nomePresidente = 'João Amoêdo';
        });
        this.noticias.getNoticias('amoedo|amoêdo').then(data => {
            this.modelNews.vetorNews = data;
        });
    }

    mapTwitter() {
        this.twitterModel.presidente = 'amoedo';
    }
    ngOnInit() {}
}
